body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Inconsolata, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App_app__12qws {
  margin: 0;
}
:root {
  --fg: black;
  --bg: white;
  --input-fg: var(--fg);
  --input-bg: rgba(200,200,200,0.1);
  --display-fg: black;
  --display-bg: white;
  --error-fg: #777;
  --sansfont: "open sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  --monofont: Inconsolata, Menlo, Monaco, Consolas, "IBM Plex Mono", "Source Code Pro", 
  "Courier New", monospace;
}

html, body {
  margin: 0;
  background-color: white;
  background-color: var(--bg);
  font-family: "open sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  font-family: var(--sansfont);
}

button {
  font-family: "open sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  font-family: var(--sansfont);
  font-size: .8em;
  align-self: baseline;
}

.FormulaEditor_formulaWrapper__2MAe0 {
  padding: 10px;
  display: grid;
  grid-template-areas:
    "title title"
    "options options"
    "input input"
    "display display";
  color: black;
  color: var(--fg);
  background-color: white;
  background-color: var(--bg);
}

.FormulaEditor_title__DKEy- {
  grid-area: title;
  font-size: 1.5em;
  font-weight: bold;
}

.FormulaEditor_formulaOptions__25AG7 {
  grid-area: options;
  display: flex;
  flex-direction: row;
}

.FormulaEditor_formulaOptions__25AG7 > div {
  margin-right: 1em;
  display: flex;
  flex-direction: row;
}

.FormulaEditor_formulaInput__11beu {
  grid-area: input;
}

.FormulaEditor_formulaInput__11beu > textarea {
  font-size: 1.2em;
  border: 2px solid black;
  border: 2px solid var(--fg);
  color: black;
  color: var(--input-fg);
  background-color: rgba(200,200,200,0.1);
  background-color: var(--input-bg);
  font-family: Inconsolata, Menlo, Monaco, Consolas, "IBM Plex Mono", "Source Code Pro", 
  "Courier New", monospace;
  font-family: var(--monofont);
}

.FormulaEditor_formulaDisplay__2Zm9A {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 20px;
  grid-area: display;
  color: black;
  color: var(--display-fg);
  background-color: white;
  background-color: var(--display-bg);
  border: 1px solid white;
  border: 1px solid var(--display-bg);
}

.FormulaEditor_formulaDisplay__2Zm9A:hover {
  border: 1px solid rgba(100,100,100,.1);
}

.FormulaEditor_formulaDisplayError__1dchW {
  padding: 10px 20px;
  grid-area: display;
  font-family: Inconsolata, Menlo, Monaco, Consolas, "IBM Plex Mono", "Source Code Pro", 
  "Courier New", monospace;
  font-family: var(--monofont);
  color: #777;
  color: var(--error-fg);
}
